export class DatabasePreminingContract extends DatabaseBaseContract {
  constructor(contractAddress) {
    super(contractAddress, CONTRACT_ABI)
  }
  async getLiquidityInfo(chainId, poolAddress) {
    let methodName = ''
    if (chainId === '1') {
      methodName = 'poolDataETH';
    } else if (chainId === '56') {
      methodName = 'poolDataBSC';
    } else if (chainId === '128') {
      methodName = 'poolDataHECO';
    }
    if (methodName !== '') {
      return await this._call(methodName, [poolAddress])
    }
  }
}